import { Link } from "react-router-dom";

const Pet = (props) => {
  const { name, animal, breed, images, location, id, gender, age } = props;

  let hero =
    "data:image/png;base64,";
  if (images.length) {
    hero = images[0].full;
  }

  return (
    <Link to={`/adopt-me/details/${id}`} className="pet">
      <div className="image-container">
        <img src={hero} alt={name} />
      </div>
      <div className="info">
        <h1>{name.toUpperCase()}</h1>
        <h2>{`${animal} - ${breed} - ${gender} - ${age} - ${location.substring(
          0,
          2
        )}`}</h2>
      </div>
    </Link>
  );
};

export default Pet;
